import React from "react";
import { css } from "@emotion/core";
import { Link } from "gatsby";
import { rhythm } from "../utils/typography";

export default function ArticleList({ edges, isFirebase }) {
  if (isFirebase) {
    return edges.map(({ node }, i) => (
      <div key={i}>
        <a
          href={node.fields.slug}
          css={css`
            text-decoration: none;
            color: inherit;
          `}
        >
          <h3
            css={css`
              margin-bottom: ${rhythm(1 / 4)};
            `}
          >
            {node.frontmatter.title}{" "}
            <span
              css={css`
                color: #555;
              `}
            >
              â€” {node.frontmatter.date.replace(",", "")}
            </span>
          </h3>
          {node.frontmatter.by && <p>By: {node.frontmatter.by}</p>}
          <p>{node.excerpt}</p>
        </a>
      </div>
    ));
  } else {
    return edges.map(({ node }, i) => (
      <div key={i}>
        <Link
          to={node.fields.slug}
          css={css`
            text-decoration: none;
            color: inherit;
          `}
        >
          <h3
            css={css`
              margin-bottom: ${rhythm(1 / 4)};
            `}
          >
            {node.frontmatter.title}{" "}
            <span
              css={css`
                color: #555;
              `}
            >
              â€” {node.frontmatter.date.replace(",", "")}
            </span>
          </h3>
          {node.frontmatter.by && <p>By: {node.frontmatter.by}</p>}
          <p>{node.excerpt}</p>
        </Link>
      </div>
    ));
  }
}
